<!-- 信维——DT柏拉图及明细看板 -->
<template>
  <div class="kanban">
    <!-- <search-bar class="oee_head" @search="search" :searchData="searchData" :searchOptions="searchOptions"
      :groupSearchType="groupSearchType"></search-bar> -->
    <search-bar @search="search" :searchData="searchData" :searchOptions="searchOptions" @getGroupSucceed="loadData"></search-bar>
    <div class="kanbanContent">
      <div class="top_content">
        <card-module title="DT柏拉图" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%" @exportExcel="exportEchart">
          <div style="width: 100%; height: 100%" ref="badness"></div>
        </card-module>
      </div>
      <div class="bottom_content">
        <card-module title="告警明细" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%" @exportExcel="exportTable">
          <div class="warn_list">
            <div class="warn_content" ref="warnContent">
              <VXETable ref="xGrid" :tableName="''" :height="tableHeight" :tableLoading="mainPage.tableLoading"
                :tableDatas="mainPage.data" :tableColumns="mainPage.columns" :highlightCurrentRow="true"
                :tablePage="mainPage.page" @sortChange="sortChange" @onTablePageChange="handleMainPageChange">
              </VXETable>
            </div>
          </div>
        </card-module>
      </div>
    </div>
  </div>
</template>

<script>
// 引入标题元素组件
import VXETable from '@/components/common/Table';
import { fontSize, parseTime } from '@/utils/common';
import CardModule from '@/components/common/kanban/CardModule';
import searchBar from './components/searchBar.vue';
import {
  STATUS_CODE, BTN_TEXT, VARIABLE, TITLE
} from '@/utils/constantsFile';
// import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { getDeviceWarnRecord, dtPlatoChart } from '@/api/xwReport';
import { exportJson2Excel } from '@/utils/exportExcel';
import { formatJson } from '@/utils/publicFunction';
import {
  // getGroupId,
  getSearchToday
  // getTimeFunc
} from '@/utils/publicFunction2';
export default {
  name: 'kanban_xw_DTplato',
  components: {
    VXETable,
    CardModule,
    searchBar
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      TITLE: TITLE,
      title: '',
      groupId: 0,
      time: '',
      dataTime: '',
      tableHeight: 400,
      searchData: {
        groupValue: [1, 2, 3, '00000190_00000005'],
        date: [new Date(getSearchToday(6) + ' 00:00:00'), new Date(getSearchToday(0) + ' 23:59:59')],
        timeKeyEnum: 'DAY'
      },
      searchOptions: [
        {
          type: 'cascader',
          key: 'groupValue'
        },
        {
          type: 'daterange',
          key: 'date',
          options: [
            {
              label: '小时',
              value: 'LESSON'
            },
            {
              label: '天',
              value: 'DAY'
            }
          ]
        }
      ],
      groupSearchType: 1,
      kanbanConfig: { title: '安灯看板', key: '', time: 15000 },
      coatingData: {},
      barChartInfo: {
        current: 1,
        size: 4
      },
      maintainAnalyse: {
        current: 1,
        size: 4
      },
      timer: null,
      timer2: null,
      mainPage: {
        show: true,
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 10,
          align: 'right',
          pageSizes: [10, 20, 50, 100, 200, 500],
          layouts: ['Sizes', 'PrevPage', 'Number', 'NextPage', 'Total'],
          perfect: true
        },
        data: [],
        columns: [
          {
            field: 'workshop',
            title: '车间',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'productionLine',
            title: '产线',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'deviceName',
            title: '设备',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'warnValue',
            title: '故障类型',
            width: 'auto',
            align: 'center',
            formatter: ({ row }) => {
              if (row.warnValue === '1') {
                return row.warnName;
              } else {
                return row.warnValue;
              }
            }
          },
          {
            field: 'warningStartTime',
            title: '报警开始时间',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'warningEndTime',
            title: '报警结束时间',
            width: 'auto',
            align: 'center',
            slots: {
              default: ({ row, column }) => {
                return [
                  <div class={row.warningEndTime ? '' : 'empty'}>
                    {row.warningEndTime}
                  </div>
                ];
              }
            }
          },
          {
            field: 'alarmDuration',
            title: '报警时长',
            width: 'auto',
            align: 'center',
            formatter: ({ row }) => {
              const startTime = new Date(row.warningStartTime);
              const endTime = new Date(row.warningEndTime);
              if (row.warningEndTime) {
                const duration = (endTime - startTime) / 1000 / 60;
                return Number(duration.toFixed(2));
              } else {
                const duration = (new Date().getTime() - startTime) / 1000 / 60;
                return Number(duration.toFixed(2));
              }
              // return '';
            }
          }
        ],
        tableLoading: false
      },
      XData: [],
      warnCount: [],
      radio: []
    };
  },
  mounted() {
    document.documentElement.style.fontSize = this.fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = this.fontSize() + 'px';
    };
    this.tableHeight = this.$refs.warnContent.clientHeight - 20;
    // this.loadData();
    // this.getExceptionCount();
    // this.timer = setInterval(() => {
    //   this.loadData();
    // }, this.kanbanConfig.time || 3000);
    // this.timer2 = setInterval(() => {
    //   this.getExceptionCount();
    // }, 8000);
  },
  created() {
    // this.loadData();
  },
  methods: {
    exportEchart() {
      const multiHeader = [];
      const header = ['', ...this.XData];
      const data = [
        ['告警数', ...this.warnCount],
        ['累计告警率', ...this.radio.map(item => item + '%')]
      ];
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: 'DT柏拉图',
        merges: []
      });
    },
    exportTable() {
      const multiHeader = [];
      const header = this.mainPage.columns.map(item => item.title);
      const filterVal = this.mainPage.columns.map(item => item.field);
      const data = formatJson(filterVal, this.mainPage.data);
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: '告警明细',
        merges: []
      });
    },
    async findList() {
      try {
        this.mainPage.tableLoading = true;
        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: [],
          size: this.mainPage.page.pageSize,
          current: this.mainPage.page.currentPage
        };

        if (this.searchData.groupValue) {
          var str = this.searchData.groupValue[
            this.searchData.groupValue.length - 1
          ];
          if (isNaN(str)) {
            conditionJson.conditionJson1.push({
              column: 'deviceCode',
              operator: 1,
              ptype: 'string',
              type: 'in',
              value: str
            });
            // param.deviceCode = str;
          } else {
            conditionJson.conditionJson1.push({
              column: 'groupId',
              operator: 1,
              ptype: 'int',
              type: 'in',
              value: str
            });
            // param.groupId = str;
          }
        }
        const param = { timeKeyEnum: this.searchData.timeKeyEnum };
        if (this.searchData.date) {
          if (this.searchData.timeKeyEnum === 'DAY') {
            const newDate = this.searchData.date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(this.searchData.date[0]);
            param.endTime = parseTime(newDate);
          } else if (this.searchData.timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(this.searchData.date[0]);
            param.endTime = parseTime(this.searchData.date[1]);
          }
        } else {
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        const data = await getDeviceWarnRecord(param, conditionJson);
        this.mainPage.data = [];
        this.mainPage.page.total = 0;
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.mainPage.data = data.rows;
          this.mainPage.page.total = data.total;
        } else {
          // showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.mainPage.tableLoading = false;
      }
    },
    async getPlatoData() {
      try {
        const { date, groupValue, timeKeyEnum } = this.searchData;
        const param = { timeKeyEnum };
        if (date) {
          if (timeKeyEnum === 'DAY') {
            const newDate = date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(newDate);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          }
        } else {
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        };
        if (groupValue) {
          var str = groupValue[groupValue.length - 1];
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        };
        const res = await dtPlatoChart(param, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(res);
          const data = res.rows || [];
          const XData = data.slice(0, 5).map(item => item.key);
          const warnCount = data.slice(0, 5).map(item => item.warnCount);
          const radio = data.slice(0, 5).map(item => item.radio);
          this.XData = XData;
          this.warnCount = warnCount;
          this.radio = radio;
          this.drawBadness(XData, warnCount, radio);
        }
      } catch (error) {
        console.log(error);
      }
    },
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.handleMainPageChange(1, this.mainPage.page.pageSize);
    },
    propertyFormat({ cellValue }) {
      return cellValue + '%';
    },
    hourFormat({ cellValue }) {
      return (cellValue / 60).toFixed(2);
    },
    handleMainPageChange(currentPage, pageSize) {
      this.mainPage.page.currentPage = currentPage;
      this.mainPage.page.pageSize = pageSize;
      this.findList();
    },
    drawBadness(xData, warnCount, radio) {
      const Badness = this.$echarts.init(this.$refs.badness);
      const option = {
        // color: EQUIPMENT_STATUS_REPORT.COLOR,
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          show: true,
          // right: '5%',
          left: 'center',
          textStyle: {
            color: '#ffffff'
          }
        },
        // barMaxWidth: '60%',
        grid: {
          top: '15%',
          left: '3%',
          right: '5%',
          bottom: '10%',
          containLabel: true
        },
        // dataZoom: [
        //   {
        //     type: 'slider',
        //     show: true,
        //     xAxisIndex: [0],
        //     bottom: 10,
        //     start: 0,
        //     end: 10,
        //     textStyle: {
        //       color: '#fff'
        //     }
        //   }
        // ],
        xAxis: [
          {
            type: 'category',
            name: '告警类型',
            data: xData,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(16),
              // color: '#D8D8D8'
              color: '#ffffff'
            },
            nameTextStyle: {
              fontSize: fontSize(18)
            },
            nameGap: '15'
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '告警数',
            // minInterval: 1,
            // splitNumber: 0.2,
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(18),
              // color: '#D8D8D8'
              color: '#ffffff'
            },
            nameTextStyle: {
              fontSize: fontSize(18)
            },
            splitLine: {
              show: false
            }
          },
          {
            type: 'value',
            name: '累计告警率%',
            // minInterval: 1,
            // splitNumber: 0.2,
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(18),
              // color: '#D8D8D8'
              color: '#ffffff'
            },
            nameTextStyle: {
              fontSize: fontSize(18)
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            name: '告警数',
            type: 'bar',
            barWidth: 25,
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#00FFE3'
                  },
                  {
                    offset: 1,
                    color: '#4693EC'
                  }
                ])
              }
            },
            label: {
              show: true,
              position: 'top',
              color: '#ffffff'
            },
            data: warnCount
          },
          {
            name: '累计告警率%',
            type: 'line',
            yAxisIndex: 1,
            // smooth: true,
            showAllSymbol: true,
            symbol: 'circle',
            symbolSize: 10,
            itemStyle: {
              color: '#058cff'
            },
            lineStyle: {
              color: '#058cff'
            },
            areaStyle: {
              color: 'rgba(5,140,255, 0.2)'
            },
            data: radio
          }
        ]
      };
      Badness.setOption(option, true);
      window.addEventListener('resize', () => {
        Badness.resize();
      });
    },
    search() {
      this.mainPage.page.pageSize = 10;
      this.mainPage.page.currentPage = 1;
      this.findList();
      this.getPlatoData();
    },
    fontSize(num = 10) {
      const clientWidth =
        window.innerWidth ||
        document.documentElement.clientWidth ||
        document.body.clientWidth;
      if (!clientWidth) return;
      const fontSize = clientWidth / 1920;
      return num * fontSize;
    },
    loadData() {
      this.findList();
      this.getPlatoData();
      // this.getDeviceState();
    }
  }
};
</script>

